* {
  font-family: 'Montserrat', sans-serif;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.incomeExpense {
  display: flex;
  justify-content: space-around;
}

.total {
  transform: translate(50%, 0);
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.positive {
  color: green;
}

.negative {
  color: red;
}